window.addEventListener('DOMContentLoaded', function() {
    (function($) {


        const $window = $(window);
        const winW = $window.width();
        const header = $('.header');

        function header_scroll(_this) {
            var _scrollTop = _this.scrollTop();
            if (_scrollTop > 0) {
                header.addClass('active');
            } else {
                header.removeClass('active');
            }
        }

        header_scroll($window);

        $window.scroll(function() {
            header_scroll($window);
        });


        //menu選單 tab 選取 --------------------------------
        if (winW > 768) {
            $('.nav_link').focusin(function() {
                $('.nav_link').removeClass('active');
                $(this).addClass('active');
            });
            $('.subnav_link').focusin(function() {
                $('.nav_link').removeClass('active');
                $(this).closest('.nav_li').find('.nav_link').addClass('active');
            });
            $('a').not('.nav_link, .subnav_link').focusin(function() {
                $('.nav_link').removeClass('active');
            });
            $('.nav_li').hover(function() {
                $(this).find('.nav_link').addClass('active');
            }, function() {
                $('.nav_link').removeClass('active');
            });
        }

        //menu選單 手機板-------------------------------------------
        if (winW <= 768) {
            const hamburger = $('.hamburger'),
                nav = $('.nav'),
                nav_link = $('.nav_link'),
                subnav_box = nav.find('.subnav_box'),
                navReset = function() {
                    nav_link.attr('o', 1);
                    nav.clearQueue();
                    subnav_box.slideUp(0);
                };
            let o = true;

            navReset();
            nav_link.click(function(e) {
                var _this = $(this),
                    _thisSubnav = _this.siblings('.subnav_box'),
                    _o = _this.attr('o');
                if (_this.attr('href').indexOf("#") != -1) {
                    TweenMax.to(nav, .2, { opacity: 0, display: 'none' });
                    hamburger.removeClass('cross');
                    o = true;
                }
                if (_thisSubnav.length <= 0) return;
                e.preventDefault();
                if (_o == 1) {
                    _thisSubnav.clearQueue();
                    _thisSubnav.slideDown();
                    _this.attr('o', 0);
                } else {
                    _thisSubnav.slideUp();
                    _this.attr('o', 1);
                }
            });


            TweenMax.set(nav, {
                opacity: 0,
                display: 'none'
            });
            hamburger.click(function() {
                if (o) {
                    navReset();
                    TweenMax.to(nav, .3, { opacity: 1, display: 'flex' });
                    hamburger.addClass('cross');
                    o = false;
                } else {
                    TweenMax.to(nav, .2, { opacity: 0, display: 'none' });
                    hamburger.removeClass('cross');
                    o = true;
                }

            });
        }
    })(jQuery);
});