window.addEventListener('DOMContentLoaded', function() {
    (function($) {

        const winW = $(window).width();

        //lightbox-------------------------------------------
        var lightbox = $('.lightbox');
        var lightbox_pic = $('.lightbox_pic');
        var lb_btn = $('.lb_btn');
        closeFn(lightbox);
        closeFn(lightbox_pic);
        lb_btn.click(function() {
            var data = $(this).data('lb');
            var _src = $(this).find('img').attr('src');
            $('#lb_pic').find('img').attr('src', _src);
            TweenLite.to($('#' + data), .4, { opacity: 1, display: 'flex', ease: Circ.easeOut });
        });


        // tab 點選
        $('.lb_btn').keypress(function(e) {
            if (e.which == 13) {
                var data = $(this).data('lb');
                TweenLite.to($('#' + data), .4, {
                    opacity: 1,
                    display: 'flex',
                    ease: Circ.easeOut,
                    onComplete: function() {
                        if (winW <= 768) return;
                        lb_acc();
                    }
                });
            }
        });


        function closeFn(lightbox) {
            var btnClose = lightbox.find('.btnClose');
            btnClose.click(function(e) {
                TweenLite.to(lightbox, .2, {
                    opacity: 0,
                    display: 'none',
                    ease: Circ.easeOut
                });
                // tab 點選 btnClose
                if (e.detail === 0) {
                    $('.arti_pic').focus();
                }
            });
            lightbox.click(function(e) {
                if ($(e.target).is(lightbox))
                    TweenLite.to(lightbox, .2, {
                        opacity: 0,
                        display: 'none',
                        ease: Circ.easeOut
                    });
            });

        }

        function lbFn(_this) {
            closeFn(_this);
            TweenLite.to(_this, .4, {
                opacity: 1,
                display: 'flex',
                ease: Circ.easeOut,
                onComplete: function() {
                    if (winW <= 768) return;
                    scrollBox();
                }
            });
        }

        function lb_acc() {

            var focusedElementBeforeModal;
            focusedElementBeforeModal = document.activeElement;

            var modal = document.querySelector('.lightbox');
            modal.addEventListener('keydown', trapTabKey);

            var focusableElementsString = 'a[href], area[href], input:not([disabled]), select:not([disabled]), textarea:not([disabled]), button:not([disabled]), iframe, object, embed, [tabindex="0"], [contenteditable]';
            var focusableElements = modal.querySelectorAll(focusableElementsString);

            focusableElements = Array.prototype.slice.call(focusableElements);

            var firstTabStop = focusableElements[0];
            var lastTabStop = focusableElements[focusableElements.length - 1];
            firstTabStop.focus();

            function trapTabKey(e) {

                if (e.keyCode === 9) {

                    // SHIFT + TAB
                    if (e.shiftKey) {
                        if (document.activeElement === firstTabStop) {
                            e.preventDefault();
                            lastTabStop.focus();
                        }

                        // TAB
                    } else {
                        if (document.activeElement === lastTabStop) {
                            e.preventDefault();
                            firstTabStop.focus();
                        }
                    }
                }

                // ESCAPE
                if (e.keyCode === 27) {
                    TweenLite.to(lightbox, .2, {
                        opacity: 0,
                        display: 'none',
                        ease: Circ.easeOut
                    });
                    focusedElementBeforeModal.focus();
                }
            }
        }

    })(jQuery);
});