import 'index.scss';
import '_lightbox';
import '_header';
import '_lazyload';



window.addEventListener('DOMContentLoaded', function() {
    (function($) {

        var winW = $(window).width();

        var tween = new TimelineMax({ delay: .5 })
            .from('.kv_center', .8, { opacity: 0, y: 50, ease: Power1.easeOut })
            .staggerFrom(['.kv_item_1', '.kv_item_2', '.kv_item_3', '.kv_item_4'], .8, { opacity: 0, ease: Power1.easeOut }, .15, '-=.2')


        //--------------------- ScrollMagic ---------------------//
        const controller = new ScrollMagic.Controller();

        // sec1
        var sec1_river = $('.sec1_river');
        var sec1_from = -10;
        var sec1_end = 0;

        var tween = new TimelineMax()
            .from('#sec1_scene', .6, { delay: .5, opacity: 0, y: -50, ease: Circ.easeOut })
            .from('.sec1_peo', .8, { opacity: 0, ease: Power1.easeOut }, '+=.2')
            .from('.sec1_letters', .8, { opacity: 0, x: -10, y: 3, ease: Power1.easeOut }, '-=.5')

        function sec1_mask() {
            if (sec1_from > 100) gsap.ticker.remove(sec1_mask);
            sec1_from += 1.2;
            sec1_end += 1.2;
            TweenMax.set(sec1_river, { maskImage: 'linear-gradient(135deg, black 0%, black ' + sec1_from + '%, transparent ' + sec1_end + '%)' })
        }

        new ScrollMagic.Scene({ triggerElement: "#trigger_sec1", reverse: false })
            .setTween(tween)
            .on("enter", function() {
                gsap.ticker.add(sec1_mask);
            })
            .addTo(controller);



        // sec2
        var sec2_river = $('.sec2_river');
        var sec2_from = -50;
        var sec2_end = -40;

        var tween = new TimelineMax()
            .from('.sec2_item_1', .8, { delay: .5, opacity: 0, ease: Power1.easeOut })
            .from('.sec2_left', .8, { opacity: 0, ease: Power0.easeOut }, '+=.4')
            .from('#sec2_scene', .6, { opacity: 0, y: -50, ease: Circ.easeOut }, '-=.2')
            .to('.sec2_item_1', .8, { opacity: 0, ease: Power1.easeOut }, '+=.4')
            .from('.sec2_item_2', .8, { opacity: 0, ease: Power1.easeOut }, '-=.0')

        function sec2_mask() {
            if (sec2_from > 100) gsap.ticker.remove(sec2_mask);
            sec2_from += 1;
            sec2_end += 1;
            TweenMax.set(sec2_river, { maskImage: 'linear-gradient(230deg, black 0%, black ' + sec2_from + '%, transparent ' + sec2_end + '%)' })
        }

        new ScrollMagic.Scene({ triggerElement: "#trigger_sec2", reverse: false })
            .setTween(tween)
            .on("enter", function() {
                gsap.ticker.add(sec2_mask);
            })
            .addTo(controller);



        // sec3
        var tween = new TimelineMax()
            .staggerFrom(['.sec3_scene_1', '.sec3_scene_2', '.sec3_scene_3'], .8, { delay: .5, opacity: 0, ease: Power1.easeOut }, .15)
            .from('.sec3_peo, .sec3_item_1', .8, { opacity: 0, y: 20, ease: Quart.easeOut })
            .to('.sec3_item_1', 2, { rotate: 10, yoyo: true, repeat: -1, ease: Power1.easeInOut }, '-=.4')
            .from('.sec3_letters', 2, { opacity: 0, y: 20, ease: Quart.easeInOut }, '-=1.5')

        new ScrollMagic.Scene({ triggerElement: "#trigger_sec3", reverse: false })
            .setTween(tween)
            .addTo(controller);



        // sec4
        var sec4_river = $('.sec4_river');
        var sec4_from = -100;
        var sec4_end = -90;

        var tween = new TimelineMax()
            .from('.sec4_house_left', .8, { opacity: 0, ease: Power1.easeOut })
            .from('.sec4_peo_left', .8, { opacity: 0, ease: Power1.easeOut }, '-=.6')
            .from('.sec4_content', .8, { opacity: 0, ease: Power1.easeOut }, '-=.4')
            .from('.sec4_house_right, .sec4_house_front', .8, { opacity: 0, ease: Power1.easeOut }, '+=.2')
            .from('.sec4_peo_right', .8, { opacity: 0, ease: Power1.easeOut }, '-=.5')
            .from('.sec4_letters', .8, { opacity: 0, x: 10, y: -30, ease: Power1.easeOut }, '+=1')

        function sec4_mask() {
            if (sec4_from > 100) gsap.ticker.remove(sec4_mask);
            sec4_from += 1;
            sec4_end += 1;
            TweenMax.set(sec4_river, { maskImage: 'linear-gradient(220deg, black 0%, black ' + sec4_from + '%, transparent ' + sec4_end + '%)' })
        }

        new ScrollMagic.Scene({ triggerElement: "#trigger_sec4", reverse: false })
            .setTween(tween)
            .on("enter", function() {
                gsap.ticker.add(sec4_mask);
            })
            .addTo(controller);


        // sec5
        var tween = new TimelineMax({ delay: 1.5 })
            .from('.sec5_swiper', 1, { opacity: 0, y: 80, ease: Quart.easeOut })
            .from('.sec5_peo', 1, { opacity: 0, y: -150, ease: Elastic.easeOut.config(.7) })

        new ScrollMagic.Scene({ triggerElement: "#trigger_sec5", reverse: false })
            .setTween(tween)
            .addTo(controller);



        // sec5_river
        var sec5_river = $('.sec5_river');
        var sec5_from = -10;
        var sec5_end = 0;

        function sec5_mask() {
            if (sec5_from > 100) gsap.ticker.remove(sec5_mask);
            sec5_from += 1;
            sec5_end += 1;
            TweenMax.set(sec5_river, { maskImage: 'linear-gradient(230deg, black 0%, black ' + sec5_from + '%, transparent ' + sec5_end + '%)' })
        }

        // sec6
        var sec6_river = $('.sec6_river');
        var sec6_from = -100;
        var sec6_end = -90;

        var tween = new TimelineMax({ delay: .5 })
            .from('#sec6_scene', .8, { opacity: 0, y: -30, ease: Power1.easeOut })
            .staggerFrom(['.sec6_peo_1', '.sec6_peo_2', '.sec6_peo_3'], .8, { opacity: 0, ease: Power1.easeOut }, .45, '+=1')
            .from('.sec6_letters', .8, { opacity: 0, x: -30, y: -10, ease: Power1.easeOut }, '+=.2')

        function sec6_mask() {
            if (sec6_from > 100) gsap.ticker.remove(sec6_mask);
            sec6_from += 1;
            sec6_end += 1;
            TweenMax.set(sec6_river, { maskImage: 'linear-gradient(140deg, black 0%, black ' + sec6_from + '%, transparent ' + sec6_end + '%)' })
        }

        new ScrollMagic.Scene({ triggerElement: "#trigger_sec6", reverse: false })
            .setTween(tween)
            .on("enter", function() {
                gsap.ticker.add(sec5_mask);
                gsap.ticker.add(sec6_mask);
            })
            .addTo(controller);



        //--------------------- Parallax ---------------------//
        var scene = document.getElementById('kv_scene');
        if (winW > 768) var parallaxInstance = new Parallax(scene);

        TweenMax.set('.kv_item_1', { x: 5, y: 20 });
        TweenMax.set('.kv_item_2', { x: -8, y: 15 });
        TweenMax.set('.kv_item_3', { x: 5, y: 15 });
        TweenMax.set('.kv_item_4', { x: 7, y: 10 });

        TweenMax.to('.kv_item_1', 3.1, { delay: 1.0, x: -5, y: -20, yoyo: true, repeat: -1, ease: Power1.easeInOut });
        TweenMax.to('.kv_item_2', 3.3, { delay: 1.2, x: 8, y: -25, yoyo: true, repeat: -1, ease: Power1.easeInOut });
        TweenMax.to('.kv_item_3', 3.4, { delay: 1.5, x: -5, y: -15, yoyo: true, repeat: -1, ease: Power1.easeInOut });
        TweenMax.to('.kv_item_4', 3.2, { delay: 1.3, x: -7, y: -20, yoyo: true, repeat: -1, ease: Power1.easeInOut });


        var scene = document.getElementById('sec1_scene');
        if (winW > 768) var parallaxInstance = new Parallax(scene);

        TweenMax.to('.sec1_item_1', 3.1, { x: -5, y: 20, yoyo: true, repeat: -1, ease: Power1.easeInOut });
        TweenMax.to('.sec1_item_2', 3.3, { x: 8, y: -35, yoyo: true, repeat: -1, ease: Power1.easeInOut });


        var scene = document.getElementById('sec2_scene');
        if (winW > 768) var parallaxInstance = new Parallax(scene);


        var scene = document.getElementById('sec3_scene');
        if (winW > 768) var parallaxInstance = new Parallax(scene);


        var scene = document.getElementById('sec4_scene');
        if (winW > 768) var parallaxInstance = new Parallax(scene);


        var scene = document.getElementById('sec6_scene');
        if (winW > 768) var parallaxInstance = new Parallax(scene);

        //--------------------- hash scroll  ---------------------//

        let _hash
        let offsetTop
        const _html = 'html, body';
        $('.nav_link').click(function(e) {
            e.preventDefault();
            _hash = $(this).attr('href');
            offsetTop = $(_hash).offset().top;
            TweenMax.to(_html, 1, { scrollTop: offsetTop, ease: Quart.easeOut });
        });


        if ($(".sec3_swiper").length > 0) {

            const $swiper = '.sec3_swiper';
            const container = $swiper + ' .swiper';
            const pagination = $swiper + ' .swiper-pagination';
            const button_next = $swiper + ' .swiper-button-next';
            const button_prev = $swiper + ' .swiper-button-prev';

            const mySwiper = new Swiper(container, {
                grabCursor: true,
                pagination: {
                    el: pagination,
                    clickable: true,
                },
                navigation: {
                    nextEl: button_next,
                    prevEl: button_prev,
                },
                a11y: {
                    enabled: true,
                    firstSlideMessage: "第一則",
                    lastSlideMessage: "最後一則",
                    paginationBulletMessage: "到第 {{index}} 則",
                },
            });
        }


        if ($(".sec5_swiper").length > 0) {

            const $swiper = '.sec5_swiper';
            const container = $swiper + ' .swiper';
            const pagination = $swiper + ' .swiper-pagination';
            const button_next = $swiper + ' .swiper-button-next';
            const button_prev = $swiper + ' .swiper-button-prev';

            const mySwiper = new Swiper(container, {
                grabCursor: true,
                pagination: {
                    el: pagination,
                    clickable: true,
                },
                navigation: {
                    nextEl: button_next,
                    prevEl: button_prev,
                },
                a11y: {
                    enabled: true,
                    firstSlideMessage: "第一則",
                    lastSlideMessage: "最後一則",
                    paginationBulletMessage: "到第 {{index}} 則",
                },
            });
        }


        //--------------------- imagesLoaded ---------------------//
        function removeLoading() {
            $(".loading").fadeOut(function() {
                $(this).remove();
            });
        }

        imagesLoaded("html, body", function() {
            removeLoading();
        });

    })(jQuery);
});